html, body{
    margin: 0;
    height: 100%;
}

#root{
    height:100%;
}

.body_MyParcels{
    background-color: rgba(189, 216, 196, 0.65);
    height: auto;
    width: 100%;
    overflow-y: scroll;
}

.container_MyParcels{
    display: flex;
    justify-content: flex-start;
    padding-top: 10px;
    padding-left: 10px;
    flex-wrap: wrap;
    gap: 15px;
    margin-left: 20px;
}

.container_MyParcels .card{
    border: black;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 40%;
}

.container_MyParcels .card:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.parcel-card_MyParcels{
    margin: 1px;
    border-color: #32443A;
    background-color: rgba(255,255,255,0.6);
}

.parcel-card_MyParcels button{
    border: none;
    border-radius: 0;
    outline: 0;
    display: inline-block;
    color: black;
    background-color: #B0C090;
    text-align: center;
    cursor: pointer;
}

.parcel-card_MyParcels button:hover{
    background-color: #3E503A;
    border-color: #3E503A;
}

.parcel_picture{
    height: 200px;
    width: 100%;
}

#myParcelsBody{
    display: flex;
    height: 93.5%
}

.buttons-editParcelModal_MyParcels{
    margin-top: 1%;
}

#rollback-editParcelModal_MyParcels,
#confirmNewCoord-editParcelModal_MyParcels{
    border-radius: 2px;
    margin-right: 3%;
}

#newA-editParcelModal_MyParcels,
#newP-editParcelModal_MyParcels{
    margin-left: 1%;
    margin-right: 5%;
}

.labels-editParcelModal_MyParcels{
    margin-top: 1%;
}

#cobertSolo-editParcelModal_MyParcels,
#utilAtSolo-editParcelModal_MyParcels,
#utilPrevSolo-editParcelModal_MyParcels{
    margin-top: 2%;
}

#backToTop {
    display: none; /* Hidden by default */
    position: fixed; /* Fixed/sticky position */
    bottom: 20px; /* Place the button at the bottom of the page */
    right: 30px; /* Place the button 30px from the right */
    z-index: 99; /* Make sure it does not overlap */
    border: none; /* Remove borders */
    outline: none; /* Remove outline */
    background-color: #3E503A; /* Set a background color */
    color: white; /* Text color */
    cursor: pointer; /* Add a mouse pointer on hover */
    padding: 15px; /* Some padding */
    border-radius: 50%;
}

#backToTop:hover {
    background-color: #32443A;
}


#backToTop{
    display: block;
}
